#header {
  position: fixed;
  display: block;
  background-color: black;
  width: 100%;
  z-index: 100 !important;
}

#header a {
  display: block;
  width: 100%;
  color: #969696;
  text-decoration: none;
  display: block;
  font-family: "Montserrat", sans-serif;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 800;
}

#header a:hover {
  color: white;
}

#header #mobile {
  position: relative;
  display: block;
  height: 50px;
}

#header #mobile #top-nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 15px;
  padding-right: 15px;
}

#header #mobile #top-nav #logo {
  width: 120px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
}

#header #mobile #top-nav #logo > a {
  height: 50px;
  width: 120px;
  display: flex;
  justify-content: center;
  align-items: x;
}

