#banner {
  margin-top: 50px;
  min-height: 800px;
  padding-bottom: 0px;
  position: relative;
}

#banner .desktop-only {
  display: none;
}

#banner #tile-bg {
  position: absolute;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  overflow: hidden;
  background-color: #000;
}

#banner #tile-bg #banner-img {
  background-image: url(/Cod_App/static/media/mobile-banner.f5f96c1e.jpg);
  height: 100%;
  background-position: top center;
  background-size: cover;
  background-repeat: no-repeat;
}

#banner #tile-content {
  position: absolute;
  width: 80%;
  bottom: 50px;
  left: 50%;
  -webkit-transform: translateX(-50%);
